body {
    width: auto;
    margin: 0;
    padding: 50px 0 0;
    color: #000;
    font-family: "Nunito Sans", sans-serif;
    background: #fff
}

.headline--text {
    font-size: 28px;
    line-height: 1.3em;
    font-weight: 600;
    margin: 0;
    padding: 20px 0
}

.read_content--title {
    font-family: "Nunito Sans", sans-serif;
    padding: 0 12px 10px
}

.read_content--title span {
    font-size: 15px;
    font-family: "Nunito Sans", sans-serif
}

.read_content--title a {
    color: #ef6e8d;
    font-weight: bold
}

.read_content--share {
    height: 50px;
    text-align: center;
    margin: 10px 0
}

.read_content--share li {
    display: inline-flex
}

.content--wrapper {
    padding: 0;
    width: 100%;
    box-sizing: border-box
}

.social-share {
    display: block;
    margin: 12px 0 20px;
    text-align: center
}

.social-share--item {
    padding: 16px 6px 1px;
    border-radius: 50%;
    margin: 0 1px;
    text-align: center
}

.social-tw {
    background-color: #1da1f2;
    padding: 16px 7px 1px
}

.social-fb {
    background-color: #3b5998
}

.social-gp {
    background-color: #dc4e41
}

.social-ln {
    background-color: #0077b5
}

.social-wa {
    background-color: #25d366
}

.social-line {
    background-color: #00b900
}

.social-line .custom-style {
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain
}

.social-line--2 {
    width: 44px;
    height: 40px;
    padding: 16px 8px 0;
    display: initial
}

.social-line--2 .custom-style--2 {
    padding: 16px 10px 0
}

.photo-info {
    display: block;
    font-size: 12px;
    font-family: "Nunito Sans", sans-serif;
    padding: 0 5px;
    color: #777;
    line-height: 1.5em;
    margin-top: 5px
}

.read-content {
    padding: 10px;
    font-family: "Roboto", sans-serif
}

.content_footer--list {
    padding-top: 40px;
    clear: both
}

.pagination {
    background-color: #eee;
    display: flex;
    font-family: "Nunito Sans", sans-serif
}

.read-content a {
    color: #ef6e8d
}

.pagination_title {
    float: left
}

.pagination_number {
    float: left
}

.pagination_number li {
    display: inline-block;
    text-align: center
}

.pagination_number li a {
    padding: 5px 10px;
    background-color: #333;
    color: white;
    line-height: 1.2em;
    font-size: 14px;
    color: white;
    display: block;
    text-decoration: none
}

.pagination_number li.active a {
    color: white;
    background: #ef6e8d
}

.artikel_credits {
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    padding: 15px 10px
}

.artikel_credits a {
    color: #000;
    text-decoration: none
}

.artikel_credits tr {
    font-weight: bold
}

.artikel_credits tr td:first-child {
    color: #ef6e8d
}

.tags {
    position: relative;
    padding: 0 30px;
    margin: 0 0 5px;
    display: block;
    font-family: "Nunito Sans", sans-serif
}

.tags::before {
    content: attr(data-tag);
    position: absolute;
    left: 15px;
    top: 0;
    color: #777;
    font-size: 16px
}

.tags li,
.tags .tags__item {
    background-color: #c1cbd4;
    display: inline-block;
    list-style: none;
    margin: 0 5px 5px 0;
    padding: 2px 5px;
    float: left;
    font-size: 12px;
    font-weight: inherit
}

.tags a {
    color: #000;
    text-decoration: none
}

.content_footer--list {
    padding: 0 10px;
    padding-top: 40px;
    clear: both
}

.artikel_terkait {
    display: block;
    clear: both;
    font-family: "Nunito Sans", sans-serif;
    font-weight: bold
}

.title_category {
    margin-top: 22px;
    margin-bottom: 22px;
    position: relative;
    height: 22px;
    width: 100%;
    border-bottom: 1px solid #444
}

.title_category .title_category_text {
    position: absolute;
    bottom: 0;
    background: #444;
    padding: 10px;
    font-family: "Nunito Sans", sans-serif;
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    text-transform: uppercase;
    margin: 0
}

.artikel_terkait li {
    padding: 8px 10px;
    list-style: none;
    font-size: 14px
}

.artikel_terkait a {
    color: #000;
    text-decoration: none
}

.terkait__title {
    font-size: inherit;
    font-weight: inherit;
    margin: 0;
    padding: 0;
    padding-bottom: 10px;
    border-bottom: 1px solid #e4e4e4
}

.clearfix:after,
.clearfix:before {
    display: table;
    line-height: 0;
    content: ""
}

.clearfix:after {
    clear: both
}

header {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 9
}

.top__link {
    display: block;
    width: 100%;
    background: #c1cbd4;
    text-align: left;
    padding: 10px 15px;
    height: 42px
}

.top__link a {
    vertical-align: top
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0
}

nav .navpanel {
    transition: transform .2s;
    -webkit-transition: transform .2s
}

nav>ul>li>.navpanel {
    position: fixed;
    top: 62px;
    left: -80%;
    width: 80%;
    bottom: 0;
    background: rgba(155, 167, 178, .95);
    z-index: 990;
    overflow-y: auto;
    overflow-x: hidden
}

nav>ul>li:hover>.navpanel {
    transform: translate3d(100%, 0, 0);
    -webkit-transform: translate3d(100%, 0, 0)
}

#closemenu {
    position: fixed;
    top: -42px;
    left: 0;
    right: 0;
    height: 42px;
    z-index: 999;
    line-height: 42px;
    padding-left: 50px;
    ;
    font-size: 36px;
    transition: transform 0s ease-in-out;
    -webkit-transition: transform 0s ease-in-out;
    -webkit-transition-delay: .2s;
    transition-delay: .2s
}

nav ul:hover~#closemenu {
    transform: translate3d(0, 42px, 0);
    -webkit-transform: translate3d(0, 42px, 0)
}

.btn-close-wrap {
    padding: 15px 0 0 13px;
    display: inline-block;
    line-height: 1;
    position: absolute;
    right: 0;
    top: 0;
    height: 27px;
    width: 28px
}

.btn-close {
    width: 15px;
    height: 15px;
    vertical-align: top
}

.menu-main {
    margin: 0 20px;
    padding: 10px 0 0;
    color: #fff;
    display: block;
    width: 100%;
    float: left;
    text-align: left;
    font-family: 'Roboto', sans-serif
}

.menu-main li {
    list-style: none;
    padding: 5px 5px
}

.menu-main a {
    color: #000;
    text-transform: uppercase;
    display: block;
    font-family: "Nunito Sans", sans-serif;
    font-weight: bold;
    text-decoration: none;
    font-size: 18px;
    margin-bottom: 5px
}

.menu-sub {
    background: rgba(155, 167, 178, .95);
    padding: 0 0 20px;
    margin-left: 20px
}

.menu-sub li {
    list-style: none;
    padding: 5px 5px
}

.menu-sub a {
    color: #fff;
    text-transform: uppercase;
    display: block;
    font-family: "Nunito Sans", sans-serif;
    font-weight: 400
}

.box_list {
    margin: 10px auto
}

.box_list_image {
    height: 120px;
    width: 120px;
    overflow: hidden;
    float: left
}

.box_list_image img {
    width: auto;
    min-height: 120px;
    position: relative
}

.box_list_caption {
    padding-left: 135px
}

.box_list_caption--category {
    font-size: 13px;
    font-weight: bold;
    text-align: left;
    color: #fff;
    background: #ef6e8d;
    display: inline-block;
    border-bottom: 1px solid #ef6e8d;
    text-transform: uppercase;
    padding: 3px 7px
}

.box_list_caption--category span {
    display: block;
    text-align: center
}

.box_list_caption--title {
    padding: 5px 0;
    max-height: 83px;
    font-family: "Nunito Sans", sans-serif;
    font-size: 16px;
    font-weight: bold;
    line-height: 1.2em;
    color: #000;
    overflow: hidden;
    margin: 0
}

.box_list_caption--time {
    font-family: "Nunito Sans", sans-serif;
    font-size: 10px;
    font-weight: normal;
    line-height: 1.82;
    letter-spacing: 0;
    color: #bfbfbf
}

.grid_12,
.grid_11,
.grid_10,
.grid_9,
.grid_8,
.grid_7,
.grid_6,
.grid_5,
.grid_4,
.grid_3,
.grid_2,
.grid_1 {
    box-sizing: border-box;
    float: left;
    padding-left: 10px;
    padding-right: 10px
}

.grid_12 {
    width: 100%
}

.row {
    padding: 15px;
    width: 100%;
    box-sizing: border-box
}

